import { Component } from '@angular/core';
import { NavController, ViewController, ModalController } from 'ionic-angular';
import { HomePage } from '../home/home';
import { CitiesPage } from '../cities/cities';
import { Geolocation } from '@ionic-native/geolocation';
import { Platform } from 'ionic-angular';
import { NativeGeocoder, NativeGeocoderReverseResult, NativeGeocoderForwardResult } from '@ionic-native/native-geocoder';
import { GeocoderService } from '../../services/geocoder.service';
import { NgModuleCompileResult } from '@angular/compiler/src/ng_module_compiler';

@Component({
    selector: 'page-location',
    templateUrl: 'location.html'
})
export class LocationPage {
    selectedCity: string = 'city';
    currentLocation: any;

    constructor(public navCtrl: NavController,
        public viewCtrl: ViewController,
        public modalCtrl: ModalController,
        private geolocation: Geolocation,
        private platform: Platform,
        private nativeGeocoder: NativeGeocoder,
        private geocoderService: GeocoderService
    ) {
    }

    selectCity() {
        let modalSelectCity = this.modalCtrl.create(CitiesPage);
        modalSelectCity.present();
        modalSelectCity.onDidDismiss(data => {
            this.selectedCity = data.selectedCity;
            console.log(data);
        });
    }

    getCurrentLoaction() {
        this.geolocation.getCurrentPosition().then(pos => {
            let currentLatitude: any = pos.coords.latitude;
            let currentLongitude: any = pos.coords.longitude;
            console.log('lat: ' + currentLatitude + ', lon: ' + currentLongitude);
            this.platform.ready().then(result => {
                this.nativeGeocoder.reverseGeocode(currentLatitude, currentLongitude).then(data => {
                    console.log(data);
                },
                    error => {
                        console.error(error);
                    });
            });
            // this.nativeGeocoder.reverseGeocode(pos.coords.latitude, pos.coords.longitude)
            // .then((result: NativeGeocoderReverseResult) => console.log(JSON.stringify(result)))
            // .catch((error: any) => console.log(error));

            // this.platform.ready().then(result => {
            //     this.geocoderService.reverseGeocode(currentLatitude, currentLongitude).then(data => {
            //         console.log(data);
            //     },
            //         error => {
            //             console.error(error);
            //         });
            // });
        });
    }
    gotoHome() {
        this.viewCtrl.dismiss();
    }

}



